              </el-form-item>
            </el-col>
            <el-col :span="4">
              <el-form-item>
                <div class="btnWrapper">
                  <el-button type="primary"></el-button>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <table class="tableIncludeBtn">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>/</td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>
              <el-button type="primary" @click="toDetail"></el-button>
            </td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>/</td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>
              <el-button type="primary"></el-button>
            </td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>/</td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>
              <el-button type="primary"></el-button>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="block" style="margin-top:20px">
        <el-pagination layout="prev, pager, next, jumper" :total="1000">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      labelPosition: 'top',
      form: {
        name: '',
        phone: '',
        borrowDeadline: '0',
        status: '0',
        borrowTimer: '',
        borrowUse: '0'
      }
    }
  },
  methods: {
    toDetail: function(params) {
      this.$router.push('/userDetail')
    }
  }
}
</script>
<style lang="scss" scoped>
$main_width:1040px;
.base {
  width: $main_width;
  .searchCondition {
    overflow: hidden;
    .btnWrapper {
      margin-top: 24px;
      margin-left: 64px;
    }
  }
}
</style>



