<template>
  <div>
    <div class="nav">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="base">
      <div class="element">
        <p></p>
        <div>
          <el-input v-model="input" placeholder="" class="input"></el-input>
        </div>
      </div>
      <div class="element">
        <p></p>
        <div>
          <el-input v-model="input" placeholder="" class="input" :maxlength="length"></el-input>
        </div>
      </div>
      <div class="element">
        <p></p>
        <div>
          <el-select v-model="value" placeholder="">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="element">
        <p></p>
        <div>
          <el-date-picker v-model="timer" type="daterange" placeholder="">
          </el-date-picker>
        </div>
      </div><br>
      <div class="btn">
        <el-button type="primary"></el-button>
      </div>
      <table>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td>/</td>
            <td></td>
            <td></td>
            <td>
              <el-button type="primary" @click="review(index)"></el-button>
              <el-button type="primary" @click="look(index)"></el-button>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="block" style="margin-top:20px">
        <el-pagination layout="prev, pager, next, jumper" :total="10">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import { userLogin } from '../../api/index'
export default {
  data() {
    return {
      input: '',
      length: 112121,
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }],
      value: '',
      timer: ''
    }
  },
  created() {
    this.getval()
  },
  methods: {
    getval() {
      let params = {
        username: 123,
        password: 123,
        inputCode: 5328
      }
      userLogin(params).then(res => {
        console.log(res.error)
      })
    },
    // 
    review() {
      this.$router.push('/basereview')
    },
    // 
    look() {
      this.$router.push('/look')
    }
  }
}
</script>

<style lang="scss">
.base {
  table {
    border: 1px solid #e1e6ef;
    thead {
      background-color: #e1e6ef;
    }
    tr th,
    tr td {
      padding: 14px 16px;
    }
    tr:hover {
      background-color: #F1F2F7;
    }
  }
  .element {
    display: inline-block;
    margin-right: 15px;
    p {
      margin-bottom: 10px;
    }
    .input {
      width: 200px;
    }
  }
  .btn {
    margin: 20px 0 20px 390px;
  }
}
</style>




